.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.intropage{
  display: flex;
  flex-direction: row;
}

#intropage-left{
  border: #61dafb 1px solid;
  height: 100vh;
  width: 50vw;
}

#intropage-right{
  border: #61dafb 1px solid;
  height: 100vh;
  width: 50vw;
}

.btn-deactive{
  font-size: 2rem;
}

.btn-active{
  font-size: 3rem;
}

#gamepage{
  display: grid;
  grid-template-columns: [first] 5% [line2] auto [line3] 5% [end];
  grid-template-rows: [row1-start] 15% [row1-end] 15% [third-line] auto [last-line];
}

#gamepage-left{
  grid-column-start: first;
  grid-column-end: line2;
  justify-self: start;

}

#gamepage-right{
  grid-column-start: line3;
  grid-column-end: end;
  justify-self: end;

}


#gamepage-middle{
  grid-column-start: line2;
  grid-column-end: line3;
  justify-self: center;

}